@import url(https://fonts.googleapis.com/css2?family=Oleo+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
/* Google Fonts. */

html {
    scroll-behavior: smooth; /* Not supported on all browsers. */
}

body {
    margin: 0;
    background-color: #f8f8f8;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

.text-div {
    padding-left: 4%;
    padding-right: 4%;
    font-family: 'Open Sans', sans-serif;
    font-size: 1.1em;
}

@font-face {
    font-family: 'Open Sans Bold';
    font-style: normal;
    font-weight: 700;
    font-display: swap;
    src: url(https://fonts.gstatic.com/s/opensans/v18/mem8YaGs126MiZpBA-UFVZ0bf8pkAg.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

.text-div > strong, .test {
    font-family: 'Open Sans Bold', sans-serif;
    font-weight: 700;
}

/* These are the amounts that feel right to me, to keep some space
   between the content and header/footer. */
.top-margin {
    margin-top: 1em;
}

.bottom-margin {
    margin-bottom: 1.8em;
}

.non-footer-content {
    min-height: 80%;
}

/* Popup container. */
.popup {
    position: relative;
    display: inline-block;
    cursor: pointer;
    z-index: 9999;
}

/* Text you're supposed to click. */
.popup-outer {
    font-size: 0.9em;
    color: #636363;
}

/* The actual popup (appears on top). */
.popup .popuptext {
    visibility: hidden;
    width: 18em;
    background-color: #636363;
    color: #fff;
    text-align: center;
    font-size: 0.85em;
    border-radius: 0.7em;
    padding: 0.5em 0.5em 0.5em 0.5em;
    position: absolute;
    z-index: 1;
    bottom: 125%;
    left: 50%;
    margin-left: -9.5em;
}

/* Popup arrow. */
.popup .popuptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
}

/* Hide and show the popup. */
.popup .show {
    visibility: visible;
    animation: fadeIn 1s;
}

@keyframes fadeIn {
    from {opacity: 0;}
    to {opacity: 1;}
}

.outer-container {
    display: grid;
    grid-template-columns: 20% auto;
    grid-template-areas: "nav content";

}

.contents-nav {
    position: -webkit-sticky; /* Safari earlier versions. */
	position: sticky;
	top: 3.2em;
	align-self: start;
}

.contents-ol {
    list-style-type: none;
}

.contents-li {
    margin-top: 1em;
}

.contents-item {
    text-decoration: none;
    color: #636363;
}

.contents-item:hover {
    color: #008388;
}

.name-title {
    padding-top: 0.2em;
    font-size: 3em;
    font-family: 'Oleo Script', 'Georgia', serif;
    background-image: linear-gradient(to right, #249141, #249c8a, #079cb3, #3464eb);
    -webkit-background-clip: text;
    -moz-background-clip: text;
    background-clip: text;
    color: transparent;
}

.section {
    position: relative;
    /* This allows linking via table of contents to arrive at the right place. */
    padding-top: 1.7em;
}

.artwork-div {
    height: 30em;
    margin-top: 2em;
    margin-bottom: 2em;
}

.artwork-image {
    /* Center. */
    display: block;
    margin-left: auto;
    margin-right: auto;
    height: 30em;
    max-width: 100%;
    object-fit: contain;
}

/* General Kenobi image has special hover effect. */
#kenobi-image img {
    position: absolute;
    left: 50%;
    transform: translate(-50%,0);
    background-color: #f8f8f8;
    transition: opacity 1s ease-in-out;
}

#kenobi-image img.top:hover {
    opacity: 0;
}

#fancy-pants-image {
    height: 22em;
}

.social-container {
    background: #eee;
    padding: 25px 50px;
}

a.social {
    margin: 0 1rem;
    transition: transform 250ms;
    display: inline-block;
}

a.social:hover {
    transform: translateY(-2px);
}

a.youtube {
    color: #eb3223;
}

a.github {
    color: #24292e;
}

a.linkedin {
    color: #2867B2;
}

.nav-bar {
    position: -webkit-sticky; /* Safari earlier versions. */
    position: sticky;
    padding-top: 0.2em;
	top: 0;
    background-color: #f8f8f8;
    z-index: 9000;
}

/* When user hovers over a navigation option, the text changes color,
   an underline appears, and the background darkens.
   When user is at a route, that navigation option text has the changed color. */

.nav-option {
    position: relative;
    display: inline-block;
    padding: 0.5em 1em 0.1em 1em;
    margin-bottom: 0.4em;
    line-height: 24px;
    height: 24px;
    color: black;
    text-decoration: none;
    font-size: 1.3em;
}

.nav-option::after {
    content: '';
    position: absolute;
    display: block;
    bottom: 0;
    left: 50%;
    width: 0;
    height: 2px;
    border-bottom: 2px solid #008388;
}

.nav-option, .nav-option::after, .nav-option-div, .nav-option-div::after {
    transition: all 0.3s linear;
}

.nav-option:hover, .active-nav-option {
    color: #008388;
}

.nav-option:hover::after {
    left: 0;
    width: 100%;
}

.nav-option-div {
    display: inline-block;
    border-radius: 0.5em;
}

.nav-option-div:hover {
    background-color: #e1e3eb;
}

#science-banner-image {
    width: 100%;
}

.left-half {
    width: 50%;
    text-align: left;
    align-self: center
}

.right-half {
    width: 50%;
    text-align: right;
    align-self: center
}

#footer-div {
    font-size: 1em;
    height: 3em;
    padding: 1em 2.5em 1em 2.5em;
    background-color: #d4dad8;
    box-shadow: 0px 500px 0px 500px #d4dad8;
    display: flex;
}

